.container {
    max-width: 1200px;
    margin: 0px auto;
}
.card {
    width: 100%;
    margin: 20px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    padding: 15px;
}
input {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 5px 15px;
    width: 250px;
}
.form-controll {
    margin: 10px;
}
.success-block {
    border: 1px solid #44db5d;
    border-radius: 5px;
    background-color: #44db6a3b;
    color: #017715;
    padding: 5px 25px;
    text-align: center;
}
.error-block {
    border: 1px solid #db4444;
    border-radius: 5px;
    background-color: #db44443b;
    color: #940101;
    padding: 5px 25px;
    text-align: center;
}
</style>