<template>
  <div class="sidebar">
    <h2 class="text-center"><span class="underscore">DRF Letters</span></h2>
    <div class="menu">
      <router-link to="/" class="menu-item">Inbox</router-link>
      <router-link to="/sended" class="menu-item">Sended messages</router-link>
      <router-link to="/recipient" class="menu-item">Received messages</router-link>
      <router-link to="/create-msg" class="menu-item">Create message</router-link>
      <router-link to="/login" class="menu-item">Login</router-link>
    </div>
  </div>
  <div class="content">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  async beforeCreate() {
    if (this.$store.state.tokens == null) {
      this.$store.state.tokens = {
          access: localStorage.getItem('access_token'),
          refresh: localStorage.getItem('refresh_token')
      }
    }
    await this.axios.post(
        "auth/verify/", {"token": this.$store.state.tokens.access}
    ).catch(error => {
      if (error.response.status == 401) {
        this.$store.commit('REFTESH_TOKENS_FROM_API');
      }
    });
  }
}
</script>

